.nav-wrapper {
    background-color: #f7f3ec;
}
.inner-wrapper {
    padding: 10px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.nav-right {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.nav-right > li{
    list-style-type: none;
    padding-left: 20px;
}
.logout {
    cursor: pointer;
    text-decoration: underline;
}